import { useState } from 'react';
import Button from '~/components/Button';
import classNames from 'classnames/bind';
import styles from './ForgotPassword.module.scss';

const cx = classNames.bind(styles);

function ForgotPassword() {
    const [checkSubmit, setcheckSubmit] = useState(true);
    const changeForm = () => {
        setcheckSubmit(!checkSubmit);
    };
    return (
        <div className={cx('wrapper')}>
            <div className={cx('container')}>
                <div className={cx('form-container')}>
                    {checkSubmit ? (
                        <div className={cx('form-container-inner')}>
                            <div className={cx('heading')}>
                                <h1 className={cx('heading-content')}>t li mt khu ca bn</h1>
                            </div>
                            <div className={cx('pane')}>
                                <div className={cx('input-group')}>
                                    <span className={cx('input-heading')}>
                                    Nhp a ch email ca bn v chng ti s gi cho bn mt lin kt  t li mt khu ca bn.
                                    </span>
                                    <input type="text" className={cx('input-email')} />
                                </div>
                                <div className={cx('button')}>
                                    <Button primary onClick={changeForm}>Xc nhn</Button>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <div className={cx('form-container-inner')}>
                            <div className={cx('heading')}>
                                <h1 className={cx('heading-content')}>c ri!</h1>
                            </div>
                            <div className={cx('pane')}>
                                <span className={cx('pane-content')}>
                                Nu ti khon quachminhnhat1711@gmail.com tn ti, mt email xc nhn  c gi. N s c mt lin kt  t li mt khu ca bn.
                                </span>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}

export default ForgotPassword;
