import classNames from 'classnames/bind';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import Button from '~/components/Button';
import config from '~/config';
import { loginUser, registerUser } from '~/services/authService';
import styles from './PopupForm.module.scss';
import { useFormik } from 'formik';
import * as Yup from 'yup';
const cx = classNames.bind(styles);
function PopupForm({ handleClose }) {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [errorResponse, setErrorResponse] = useState('');
    const Login = useFormik({
        initialValues: {
            email: '',
            password: '',
        },
        validationSchema: Yup.object({
            email: Yup.string()
                .required('Bt buc!')
                .matches(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/, 'Vui lng nhp mt a ch email hp l!'),
            password: Yup.string()
                .required('Bt buc!')
                .matches(
                    /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+])[A-Za-z\d][A-Za-z\d!@#$%^&*()_+]{7,19}$/,
                    'Mt khu phi c 7-19 k t v cha t nht mt ch ci, mt s v mt k t c bit!',
                ),
        }),
        onSubmit: (values) => {
            const newUser = {
                Email: values.email,
                Password: values.password,
            };
            const fetchApi = async () => {
                const res = await loginUser(newUser, dispatch, navigate);
                setErrorResponse(res)
            };
            fetchApi();
        },
    });
    const Register = useFormik({
        initialValues: {
            email: '',
            password: '',
            confirmPassword: '',
        },
        validationSchema: Yup.object({
            email: Yup.string()
                .required('Bt buc!')
                .matches(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/, 'Vui lng nhp mt a ch email hp l!'),
            password: Yup.string()
                .required('Bt buc!')
                .matches(
                    /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+])[A-Za-z\d][A-Za-z\d!@#$%^&*()_+]{7,19}$/,
                    'Mt khu phi c 7-19 k t v cha t nht mt ch ci, mt s v mt k t c bit!',
                ),
            confirmPassword: Yup.string()
                .required('Bt buc!')
                .oneOf([Yup.ref('password'), null], 'Mt khu nhp li khng trng khp!'),
        }),
        onSubmit: (values) => {
            console.log(values)
            const newUser = {
                Email: values.email,
                Password: values.password,
                ConfirmPassword: values.confirmPassword,
            };
            const fetchApi = async () => {
                const res = await registerUser(newUser, dispatch, navigate);
                setErrorResponse(res)
            };
            fetchApi();
        },
    });
    const [isRegister, setIsRegister] = useState(true);
    const toggleForm = () => {
        setIsRegister(!isRegister);
        setErrorResponse()
    };

    return (
        <div className={cx('actions')}>
            {isRegister ? (
                <div className={cx('popup-box')}>
                    <div className={cx('box')}>
                        <span className={cx('close_icon')} onClick={handleClose}>
                            x
                        </span>
                        <form className={cx('form-submit')} onSubmit={Login.handleSubmit}>
                            <div className={cx('header')}>
                                <h1>ng nhp</h1>
                                <Button text rounded outline onClick={toggleForm}>
                                    ng k
                                </Button>
                            </div>
                            <div className={cx('email')}>
                                <span className={cx('content')}>a ch email </span>
                                <input
                                    id="email"
                                    name="email"
                                    value={Login.values.email}
                                    onChange={Login.handleChange}
                                    className={cx('input_login')}
                                    type="email"
                                />
                                {Login.errors.email && (
                                    <div className={cx('errorBox')}>
                                        <p className={cx('errorMsg')}>* {Login.errors.email} </p>
                                    </div>
                                )}
                            </div>
                            <div className={cx('password')}>
                                <span className={cx('content')}>Mt khu</span>
                                <input
                                    id="password"
                                    name="password"
                                    value={Login.values.password}
                                    onChange={Login.handleChange}
                                    className={cx('input_login')}
                                    type="password"
                                />
                                {Login.errors.password && (
                                    <div className={cx('errorBox')}>
                                        <p className={cx('errorMsg')}>* {Login.errors.password} </p>
                                    </div>
                                )}
                            </div>
                            <div className={cx('forgotorsave')}>
                                {/* <div className={cx('check')}>
                                    <input type="checkbox" className={cx('checkbox')} />
                                    <span className={cx('staysigned')}> Stay signed in</span>
                                </div> */}
                                <Link to={config.routes.forgotpassword} className={cx('forgotpass')}>
                                    Qun mt khu ?
                                </Link>
                            </div>
                            <div className={cx('sign_in')}>
                                <Button type="submit" login rounded primary >
                                    ng nhp
                                </Button>
                            </div>
                            {errorResponse ? (
                                <div className={cx('errorResponse-box')}>
                                    <p className={cx('errorResponse-alert')}>{errorResponse}</p>
                                </div>
                            ):""}
                        </form>
                        {/* <div className={cx('or')}>
                            <span>OR</span>
                        </div>
                        <div className={cx('sign_in')}>
                            <Button login rounded>
                                Continue with Google
                            </Button>
                        </div> */}
                    </div>
                </div>
            ) : (
                <div className={cx('popup-box')}>
                    <div className={cx('box')}>
                        <span className={cx('close_icon')} onClick={handleClose}>
                            x
                        </span>
                        <div className={cx('header')}>
                            <h1>To ti khon</h1>
                            <Button text rounded outline onClick={toggleForm}>
                                    ng nhp
                                </Button>
                        </div>
                        {/* <div className={cx('header')}>
                            <h2>Registration is easy.</h2>
                        </div> */}

                        <form className={cx('form-submit')} onSubmit={Register.handleSubmit}>
                            <div className={cx('email')}>
                                <span className={cx('content')}>a ch email</span>
                                <input
                                    id="email"
                                    name="email"
                                    value={Register.values.email}
                                    onChange={Register.handleChange}
                                    className={cx('input_login')}
                                    type="email"
                                />
                                {Register.errors.email && (
                                    <div className={cx('errorBox')}>
                                        <p className={cx('errorMsg')}>* {Register.errors.email} </p>
                                    </div>
                                )}
                            </div>
                            <div className={cx('password')}>
                                <span className={cx('content')}>Mt Khu</span>
                                <input
                                    id="password"
                                    name="password"
                                    value={Register.values.password}
                                    onChange={Register.handleChange}
                                    className={cx('input_login')}
                                    type="password"
                                />
                                {Register.errors.password && (
                                    <div className={cx('errorBox')}>
                                        <p className={cx('errorMsg')}>* {Register.errors.password} </p>
                                    </div>
                                )}
                            </div>
                            <div className={cx('password')}>
                                <span className={cx('content')}>Nhp li mt khu</span>
                                <input
                                    id="confirmPassword"
                                    name="confirmPassword"
                                    value={Register.values.confirmPassword}
                                    onChange={Register.handleChange}
                                    className={cx('input_login')}
                                    type="password"
                                />
                                {Register.errors.confirmPassword ?(
                                    <div className={cx('errorBox')}>
                                        <p className={cx('errorMsg')}>* {Register.errors.confirmPassword} </p>
                                    </div>
                                ):""}
                            </div>

                            <div className={cx('sign_in')}>
                                <Button type="submit" login rounded primary >
                                    ng k
                                </Button>
                            </div>
                            {errorResponse ? (
                                <div className={cx('errorResponse-box')}>
                                    <p className={cx('errorResponse-alert')}>{errorResponse}</p>
                                </div>
                            ):""}
                        </form>
                    </div>
                </div>
            )}
        </div>
    );
}

export default PopupForm;
