import classNames from 'classnames/bind';
import Button from '~/components/Button';
import styles from './Shipping.module.scss';

const cx = classNames.bind(styles);

function Shipping() {
    return (
        <div className={cx('wrapper')}>
            <div className={cx('body')}>
                <form className={cx('form')}>
                    <h1 className={cx('form-tittle')}>Nhp a ch giao hng ca bn</h1>
                    <div className={cx('form-group')}>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">H v tn:</label>
                            <select className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">S in thoi:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">Tnh/Thnh ph:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">Qun huyn:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">Phng x:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">a ch nhn hng:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                        <div className={cx('form-group-item')}>
                            <label className={cx('form-group-item-label')} htmlFor="">Loi a ch:</label>
                            <input className={cx('form-group-item-input')} type="text" />
                        </div>
                    </div>
                    <div className={cx('form-group-submit')}><Button primary rounded login>Tip tc thanh ton</Button></div>
                </form>
            </div>
        </div>
    );
}

export default Shipping;
